@import "../../../styles/general.scss";

body{
    margin: auto 0; 
}

.Conversation-page-wrap{
    display: flex;
    width: 100vw;
    height: 100vh;
    background-color: blue; 
}
  
.chat-send-profile-status-wrap{
    @extend %flex-display;
    flex-direction: column;
    flex-grow: 2;
    padding: 0em;
    background: rgb(240, 16, 16);
}

.chat-send-profile-wrap{
    @extend %flex-display;
    flex-grow: 3;
    background-color: brown;
}

.chat-send-wrap{
    @extend %flex-display;
    flex-direction: column;
    flex-grow: 3;
     background-color: cyan; 
}

.ContactList-wrap{
    @extend %flex-display;
    flex-direction: column;
    flex-grow: .22;
    height: 100vh;
    background-color: violet;
}